/**
  * 判断元素是否在可视范围
  */
function isVisible(ele) {
   let windowHeight = window.innerHeight
   let position = ele.getBoundingClientRect()
   // 当元素的top偏移量小于页面大小并且大于高度的负数
   if (position.top < windowHeight && position.top > -position.height) {
      return true
   }
   return false
}
/**
 * 对dom添加动画类
 */
function moving(el,data) {
   if(data){
      if (el && isVisible(el)) { // 元素存在，元素未被加载，元素可见
         el.classList.add(data.value.name)
      } else {
         el.classList.remove(data.value.name)
      }
   }
}
export default {
   install(Vue, options) {
      Vue.directive('isInScreen', {
         bind: function (el, binding, vnode) {
            if( el ){
               window.addEventListener('scroll', function () {
                  moving(el, binding)
               })
            }
         },
         inserted: function (el, binding, vnode) {
            moving(el)
         }
      })
   }
}