<template>
	<div class="home">
		<div class="quotes display-flex animated fadeInLeft" v-isInScreen='{name:"fadeInLeft"}'>
			<div class="quoteItem flex-1" v-for="(item,index) in quotes" :key="index">
				<a class="anBg">
					<img :src="item.imgUrl" alt="">
				</a>
				<p class="con" v-if="item.con">{{item.con}}</p>
				<p class="auth" v-if="item.auth">{{item.auth}}</p>
				<div class="walk" v-else @mouseenter='showHandle($event)' @mouseleave='hideHandle($event)'>
					<div class="walkGif" :style="{'animation-play-state': animationState}"></div>
					<div class="handle display-flex flex-between">
						<div class="on iconfont icon-bofang" @click="runAnimate"></div>
						<div class="stop iconfont icon-zanting" @click="stopAnimate"></div>
					</div>
				</div>
			</div>
		</div>
		<div class="aticles">
			<articleItem :item-data='item' v-for="(item,index) in aticles" :key="index"></articleItem>
		</div>
		<Pagination background layout="prev, pager, next" :page-size="Number(param.page_size)" :total="totalPage"
						@current-change = pageChange
		></Pagination>
	</div>
</template>

<script>
import { Pagination } from 'element-ui';
import articleItem from "../../components/aticleItem"
export default {
	name: 'home',
	data(){
		return{
			animationState:'running',   //  paused
			quotes:[
				{imgUrl: require('./imgs/anPic1.jpg'),con:'不管前方的路有多苦，只要走的方向正确，不管多么崎岖不平，都比站在原地更接近幸福。',auth:'— —宫崎骏《千与千寻》'},
				{imgUrl: require('./imgs/anPic2.jpg'),con:'',auth:''},
				{imgUrl: require('./imgs/anPic3.jpg'),con:'我不知道离别的滋味是这样凄凉，我不知道说声再见要这么坚强。',auth:'— —宫崎骏《千与千寻》'},
			],
			aticles:[],
			totalPage:0,
			param:{
				page_no: '1',
				page_size: '4'
			}
		}
	},
	created () {
		this.getDatas()
	},
	methods: {
		runAnimate(){
			this.animationState = 'running'
		},
		stopAnimate(){
			this.animationState = 'paused'
		},
		showHandle(e){
			e.srcElement.lastChild.style.opacity = '1'
		},
		hideHandle(e){
			e.srcElement.lastChild.style.opacity = '0'
		},
		pageChange(val){
			this.param.page_no = String(val)
			this.getDatas()
		},
		getDatas(){
			this.$http.post('/home/aticles/',this.param).then(ret =>{
				this.totalPage = ret.data.total
				this.aticles = ret.data.content
				this.$nextTick(()=>{
					document.body.scrollTop = 0;
					document.documentElement.scrollTop = 0;
				})
			})
		},
	},
	components: {
		Pagination,articleItem
	}
}
</script>

<style lang="scss" scoped>
.home{
	.quotes{
		margin-bottom: 20px;
		.quoteItem{
			&:first-child{
				padding-right:15px;
			}
			&:last-child{
				padding-left:15px;
			}
			&:nth-child(2){
				padding: 0 15px;
			}
			.anBg{
				display: block;
				margin-bottom: 30px;
				overflow: hidden;
				img{
					width: 100%;
					transition: all 1s ease;
				}
				&:hover{
					img{
							transform: scale(1.1)
					}
				}
			}
			.con{
				color: #666;
				padding: 0 15px;
				line-height: 20px;
				margin-bottom: 30px;
			}
			.auth{
				text-align: right;
				font-size: 16px;
				font-weight: 500;
			}
			.walk{
				.walkGif{
					width: 90px;
					height: 90px;
					background: url("./imgs/zz.gif") no-repeat;
					background-size: cover;
					animation: moving 6s ease-in-out infinite;
				}
				@keyframes moving {
					0%{
						transform: translate(0) rotateY(180deg)
					}
					49.5%{
						transform: translate(210%) rotateY(180deg)
					}
					50%{
						transform: translate(210%)
					}
					99.5%{
						transform: translate(0%)
					}
					100%{
						transform: translate(0%) rotateY(180deg)
					}
				}
				.handle{
					opacity: 0;
					width: 120px;
					height: 30px;
					margin: 30px auto 0;
					border-radius: 15px; 
					transition: all 1s ease-in-out;
					background: radial-gradient(80px at 40% 0px, rgba(19, 17, 17, 0.4),#000);
					>div{
						width: 60px;
						color: gold;
						line-height: 30px;
						text-align: center;
					}
				}
			}
		}
	}
}
</style>

