export function sessionData(...params) {
   
   if (params.length > 2){
      console.error('参数长度超标：最多接受两位参数');
   } else if (params.length == 1){
      let value =  sessionStorage.getItem(params[0])
      return value
   } else if (params.length == 2) {
      sessionStorage.setItem(params[0], params[1])
   }
}