<template>
	<div id="app">
		<header>
			<p class="logoTit animated fadeInRight"  v-isInScreen='{name:"fadeInRight"}'>不求共赏, 只求留香</p>
			<div class="navBar display-flex">
				<div class="nav flex-1 animated fadeInUp" v-isInScreen='{name:"fadeInUp"}'>
					<div class="navItem" v-for="(item,index) in navData" :key="index" 
							@mouseenter='navEnter(index)' @mouseleave='navLeave(index)' @click="goPage(item.nav_pageUrl)"> 
						<p>{{item.nav_name}}</p>
						<p v-show="navIndex == index" class="animatedNav fadeInDown" 
								:class="{'fadeInDown':navIndex == index}">{{item.nav_eng}}</p>
					</div>
				</div>
				<div class="searchBox display-flex">
					<input class="flex-1" v-model="searchArticle" type="search" name="" id="" placeholder="搜索文章">
					<button @click="goSearchPage()">Search</button>
				</div>
			</div>
		</header>
		<div class="appContent">
			<router-view/>
		</div>
		<footer>
			<div class="footWrap">
				<div class="listItem" v-for="(item,index) in footData" :key="index">
					<p class="tit">{{item.tit}}</p>
					<p v-for="child in item.desc" :key="child">{{child}}</p>
				</div>
			</div>
			<!-- <p>豫ICP备18027620号 | 博客已运行0 年 321 天 17 小时 53 分钟 27 秒 | 网站统计</p> -->
			<p>豫ICP备xxxxxxx号 | 博客已运行0 年 0 天 0 小时 0 分钟 0 秒 | 网站统计</p>
		</footer>
		<myDialog v-if="showDialog"></myDialog>
	</div>
</template>

<script>
import { Message } from 'element-ui';
import myDialog from "./components/myDialog.vue";
import { sessionData } from './js/public'
export default {
	data(){
		return{
			navData:[],
			footData:[
				{tit:'关于我',desc:['github','csdn']},
				{tit:'CDN资源库',desc:['bootcdn','Staticfile CDN']},
				{tit:'常用教程',desc:['w3school','菜鸟教程']},
				{tit:'云服务器',desc:['阿里云','腾讯云']},
			],
			navIndex: -1,
			searchArticle:'',
			dialogFormVisible:true,
			showDialog: false,
		}
	},
	created () {
		this.getDatas()
	},
	computed: {
		stopScroll(){
			document.getElementById('app').style.overflow = 'hidden'
		}
	},
	methods: {
		getDatas(){
			this.$http.post('/home/nav/',{}).then(ret =>{
				this.navData = ret.data
			})
		},
		goSearchPage(){
			if( !this.searchArticle ){
				Message({
					message: '必须输入关键字才能搜索！！！',
					type: 'error'
				});
				return
			}
			sessionData('searchArticle',this.searchArticle )
			this.$router.push('/Search');
		},
		goPage(url){
			this.$router.push(url);
		},
		navEnter(index){
			this.navIndex = index;
		},
		navLeave(){
			this.navIndex = -1;
		},
	},
	components: {
		Message,myDialog
	}
}
</script>

