<template>
   <div class="dialogBox display-flex flex-coloum align-center" ref="dialogBox">
      <div class="main-container" v-if="!isShow">
         <div class="close iconfont icon-searchclose" @click="closeDialog"></div>
         <div class="tab display-flex flex-between align-center justify-center">
            <div class="login" @click="showLogin('login')">登录</div>
            <div class="dot flex-1">·</div>
            <div class="regist"  @click="showLogin('regist')">注册</div>
            <div class="borderBot border-left" ref="botBorder"></div>
         </div>
         <el-form v-if="tabStatus == 'regist'" :model="ruleForm" :rules="rules" ref="ruleForm" label-width="80px" class="demo-ruleForm">
            <el-form-item label="你的昵称" prop="name">
               <el-input v-model="ruleForm.name" maxlength="20" show-word-limit placeholder="请输入您的昵称"></el-input>
            </el-form-item>
            <el-form-item prop="phone">
               <span slot="label" style="letter-spacing: 7px;">手机号</span>
               <el-input v-model="ruleForm.phone" maxlength="11" show-word-limit placeholder="请输入手机号"></el-input>
            </el-form-item>
            <el-form-item prop="email">
               <span slot="label" style="letter-spacing: 12px;">邮 箱</span>
               <el-input v-model="ruleForm.email" placeholder="请输入邮箱"></el-input>
            </el-form-item>
            <el-form-item prop="psd">
               <span slot="label" style="letter-spacing: 12px;">密 码</span>
               <el-input v-model="ruleForm.psd" minlength="6" maxlength="15" placeholder="请输入密码"></el-input>
            </el-form-item>
            <el-form-item label="确认密码" prop="checkPsd">
               <el-input v-model="ruleForm.checkPsd" minlength="6" maxlength="15" placeholder="请再次输入密码"></el-input>
            </el-form-item>
            <div class="regist" @click="regist('ruleForm')">注册</div>
         </el-form>
         <el-form v-else :model="ruleForm" :rules="rules" ref="ruleForm" label-width="80px" class="demo-ruleForm">
            <el-form-item prop="phone">
               <span slot="label" style="letter-spacing: 7px;">手机号</span>
               <el-input v-model="ruleForm.phone" maxlength="11" show-word-limit placeholder="请输入手机号或邮箱"></el-input>
            </el-form-item>
            <el-form-item prop="psd">
               <span slot="label" style="letter-spacing: 12px;">密 码</span>
               <el-input v-model="ruleForm.psd" minlength="6" maxlength="15" placeholder="请输入密码"></el-input>
            </el-form-item>
            <div class="regist" @click="regist('ruleForm')">登录</div>
         </el-form>
      </div>
      <slot></slot>
   </div>
</template>

<script type="text/ecmascript-6">
export default {
   props: {
        txtcontent:{
            type: String,
            default: ""
        },
        btnText:{
            type: String,
            default: "好 的"
        },
        closeSelf:{
            type: Boolean,
            default: true
        },
        contentFontSize:{
            type: String,
            default: 'midum-white-txt'
        }
    },
   data() {
      var validatePass = (rule, value, callback) => {
         let reg = /^[a-zA-Z][0-9a-zA-Z_]{3,17}$/
         if (value === '') {
            callback(new Error('请输入密码'));
         } 
         else if( value.length < 6 || value.length > 15 ){
            callback(new Error('请输入6-15位密码'));
         } else if( !reg.test(value) ){
            callback(new Error('请输入合法密码'));
         }
         else {
            if (this.ruleForm.checkPsd !== '') {
               this.$refs.ruleForm.validateField('checkPass');
            }
            callback();
         }
      };
      var validatePass2 = (rule, value, callback) => {
         if (value === '') {
            callback(new Error('请再次输入密码'));
         } else {
            if (this.ruleForm.psd == '') {
               this.$refs.ruleForm.validateField('psd');
            }else if( value !== this.ruleForm.psd ){
               callback(new Error('两次输入密码不一致!'));
            }
            callback();
         }
      };
      var validatePhone = (rule,value,callback) => {
         let reg = /^[1](([3][0-9])|([4][5-9])|([5][0-3,5-9])|([6][5,6])|([7][0-8])|([8][0-9])|([9][1,8,9]))[0-9]{8}$/
         if( value === '' || value.length < 11 ){
            callback(new Error('请输入11位手机号码'));
         }else {
            if( !reg.test(value) ){
               callback(new Error('手机号不合法，请重新输入'));
            }
         }
      }
      return {
         tabStatus:'',
         ruleForm: {
            name: '',
            phone:'',
            email:'',
            psd:'',
            checkPsd:'',
         },
         rules: {
          name: [
            { required: false, message: '请输入活动名称', trigger: 'blur' },
            { min: 3, max: 20, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          email: [
            { required: false, message: '请输入邮箱地址', trigger: 'blur' },
            { type: 'email', message: '请输入正确的邮箱地址', trigger: 'blur' }
          ],
          phone: [
            { validator: validatePhone, trigger: 'blur' }
          ],
          psd: [
             { validator: validatePass, trigger: 'blur' }
          ],
          checkPsd: [
             { validator: validatePass2, trigger: 'blur' }
          ]
        }
      }
   },
   computed: {
      isShow () {
         return this.$slots.default
      }
    },
   methods: {
      showLogin(type){
         console.log(this.$refs)
         this.tabStatus = type
         if( type == 'login' ){
            this.$refs.botBorder.classList.add('border-left')
            this.$refs.botBorder.classList.remove('border-right')
         }else{
            this.$refs.botBorder.classList.remove('border-left')
            this.$refs.botBorder.classList.add('border-right')
         }
      },
      regist(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      closeDialog () {
         if(this.$refs.dialogBox){
               this.$refs.dialogBox.style.display = 'none'
         }
         
         this.$emit('onClose');
        }
   },
   components: {
      
   }
}
</script>

